import React, { useState } from "react";
import {Link, NavLink} from "react-router-dom";
import Joi, {number} from "joi";
import axios from "axios";

import InputPassword from "../components/InputPassword";
import FormErrorMessage from "../components/FormErrorMessage";
import WaitAccountActivation from "./WaitAccountActivation";
import Button from "bootstrap/js/src/button";

const formValueDefault = {
  firstName: "",
  lastName: "",
  email: "",
  password: "",
  passwordConfirm: "",
  remember: true,
  ownerType: 0,
};

const formErrorDefault = {
  firstName: "",
  lastName: "",
  email: "",
  password: "",
  passwordConfirm: "",
};
const baseUrl = "https://api.antontig.beget.tech";

const schema = Joi.object({
  firstName: Joi.string().min(4).max(20).required().messages({
    "string.empty": "    ",
    "string.min": `     4 `,
    "string.max": `     20 `,
  }),
  lastName: Joi.string().min(4).max(20).required().messages({
    "string.empty": "    ",
    "string.min": `     4 `,
    "string.max": `     20 `,
  }),
  email: Joi.string()
    .email({ tlds: { allow: false } })
    .min(4)
    .required()
    .messages({
      "string.empty": "Email     ",
      "string.min": `Email      4 `,
      "string.max": `Email      20 `,
      "string.email": ` Email   `,
    }),
  password: Joi.string()
    .pattern(/.*[A-Z].*/)
    .pattern(/.*[0-9].*/)
    .min(8)
    .required()
    .messages({
      "string.empty": "    ",
      "string.pattern.base":
        "        ",
      "string.min": `     8 `,
      "string.max": `     20 `,
    }),
  passwordConfirm: Joi.string().required().messages({
    "string.empty": "     ",
  }),
  ownerType: Joi.number(),
  remember: Joi.boolean(),
});

export default function Entrance() {
  const [formValue, setFormValue] = useState(formValueDefault);
  const [ownerType, setOwnerType] = useState(formValueDefault.ownerType);
  const [formErrors, setFormErrors] = useState(formErrorDefault);

  const handleFormChange = (e) => {
    setFormValue((prev) => {
      return {
        ...prev,
        [e.target.name]:
          e.target.type === "checkbox" ? e.target.checked : e.target.value,
      };
    });
    setFormErrors((prev) => {
      return { ...prev, [e.target.name]: "" };
    });
  };

  const handleFormSubmit = async (e) => {
    e.preventDefault()
    const result = schema.validate(formValue, { abortEarly: false });
    if (result.error) {
      handleFormErrors(result.error.details);
      return;
    }

    if (formValue.password !== formValue.passwordConfirm) {
      return setFormErrors((prev) => {
        return { ...prev, passwordConfirm: "  " };
      });
    }

    try {
      const response = await axios.post(`${baseUrl}/api/auth/register`, {
        ...formValue,
        ownerType,
      });
      alert(" ")
    } catch (error) {
      console.log(error.message)
      setFormErrors((prev) => {
        return {
          ...prev,
          email: "    .  ",
        };
      });
    }
  };

  const handleFormErrors = (errors) => {
    errors.forEach((formField) => {
      setFormErrors((prev) => {
        return { ...prev, [formField.path[0]]: formField.message };
      });
    });
  };

  return (
    <main className="account py-sm-3 py-md-4 py-lg-5">
      <section className="container">
        <Link to="/" className="d-flex align-items-center mt-3 mb-4 mb-sm-5">
          <svg
            width="12"
            height="23"
            viewBox="0 0 12 23"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <line
              y1="-1"
              x2="14.5309"
              y2="-1"
              transform="matrix(-0.660679 -0.750669 0.709114 -0.705094 11.6006 21)"
              stroke="#C59C7E"
              strokeWidth="2"
            />
            <line
              y1="-1"
              x2="14.5309"
              y2="-1"
              transform="matrix(-0.660679 0.750669 0.709114 0.705094 11.6006 2)"
              stroke="#C59C7E"
              strokeWidth="2"
            />
          </svg>
          <span className="fs-09 ms-1 ms-md-3"> </span>
        </Link>
        <div className="row justify-content-center">
          <div className="col-lg-9">
            <form className="entrance">
              <h1 className="text-center mb-4 mb-xxl-5"></h1>
              <div className="row">
                <div className="col-md-3 fs-11 title-req mt-4 mt-sm-5 mb-3 m-md-0">
                  <span data-for="owner" data-status={false}>
                     *:
                  </span>
                </div>
                <div className="col-md-9 mb-5">
                  <div className="d-flex align-items-center">
                    <div className="me-5">
                      <label>
                        <input
                          type="radio"
                          name="ownerType"
                          value="0"
                          defaultChecked={true}
                          onChange={handleFormChange}
                        />
                        <span className="fs-11 ms-2"></span>
                      </label>
                    </div>
                    <div>
                      <label>
                        <input
                          type="radio"
                          name="ownerType"
                          value="1"
                          onChange={handleFormChange}
                        />
                        <span className="fs-11 ms-2"></span>
                      </label>
                    </div>
                  </div>
                </div>
              </div>
              <div className="row align-items-center mb-3 mb-sm-4 mb-xxl-5">
                <div className="col-sm-3 fs-11 mb-1 mb-sm-0">:</div>
                <div className="col-sm-9">
                  <input
                    placeholder=""
                    className="fs-11"
                    name="firstName"
                    value={formValue.firstName}
                    onChange={handleFormChange}
                  />
                  <FormErrorMessage>{formErrors.firstName}</FormErrorMessage>
                </div>
              </div>
              <div className="row align-items-center mb-3 mb-sm-4 mb-xxl-5">
                <div className="col-sm-3 fs-11 mb-1 mb-sm-0">:</div>
                <div className="col-sm-9">
                  <input
                    placeholder=""
                    className="fs-11"
                    name="lastName"
                    value={formValue.lastName}
                    onChange={handleFormChange}
                  />
                  <FormErrorMessage>{formErrors.lastName}</FormErrorMessage>
                </div>
              </div>
              <div className="row align-items-center mb-3 mb-sm-4 mb-xxl-5">
                <div className="col-sm-3 fs-11 mb-1 mb-sm-0">Email:</div>
                <div className="col-sm-9">
                  <input
                    placeholder="mail@mail.ru"
                    className="fs-11"
                    name="email"
                    value={formValue.email}
                    onChange={handleFormChange}
                  />
                  <FormErrorMessage>{formErrors.email}</FormErrorMessage>
                </div>
              </div>
              <div className="row align-items-center mb-3 mb-sm-4 mb-xxl-5">
                <div className="col-sm-3 fs-11 mb-1 mb-sm-0">:</div>
                <div className="col-sm-9">
                  <InputPassword
                    name="password"
                    visible={false}
                    onChange={handleFormChange}
                    value={formValue.password}
                  />
                  <FormErrorMessage>{formErrors.password}</FormErrorMessage>
                </div>
              </div>
              <div className="row align-items-center mb-3 mb-sm-4 mb-xxl-5">
                <div className="col-sm-3 fs-11 mb-1 mb-sm-0">
                   :
                </div>
                <div className="col-sm-9">
                  <InputPassword
                    name="passwordConfirm"
                    visible={false}
                    onChange={handleFormChange}
                    value={formValue.passwordConfirm}
                  />
                  <FormErrorMessage>
                    {formErrors.passwordConfirm}
                  </FormErrorMessage>
                </div>
              </div>
              <div className="row justify-content-end mb-3 mb-sm-4 mb-xxl-5">
                <div className="col-sm-9">
                  <label className="fs-11">
                    <input
                      type="checkbox"
                      name="remember"
                      defaultChecked={formValue.remember}
                      value={formValue.remember}
                      onChange={handleFormChange}
                    />
                    <span className="ms-3"> </span>
                  </label>
                </div>
              </div>
              <div className="row justify-content-center">
                <div className="col-sm-8 col-lg-6">
                   <button
                    className="btn btn-1 fs-11 w-100 text-uppercase mb-4"
                    onClick={handleFormSubmit}
                  >
                     
                  </button>
                </div>
                <div className="col-12">
                  <div className="fs-09 text-center gray-3 mb-4">
                       ,  
                      
                  </div>
                  <div className="fs-11 text-center">
                       ?{" "}
                    <Link to="/entrance" className="color-1 bb-1">
                      
                    </Link>
                  </div>
                </div>
              </div>
            </form>
          </div>
        </div>
      </section>
    </main>
  );
}
